import Vue from 'vue'
import App from './App'
import Router from 'vue-router'

import newestarticles from './components/newestarticles'
import body from './components/body'
Vue.use(Router)

Vue.config.productionTip = false
var router = new Router({
    routes: [{
        path: '/',
        component: body
    }, {
        path: '/newest',
        component: newestarticles
    }]
})
new Vue({
    el: '#app',
    components: {
        App
    },
    router,
    template: '<App/>'
})


