<template>
	<header>
		<div class="ground_bg"></div>
		<nav>
			<transition name="shownav">
				<ul class='navgationlist' v-show="navshow">
					<li v-for='(item,index) in navgationlist' @click='navShow'>
						<router-link :to='item.url'>{{item.title}}</router-link>
					</li>
				</ul>
			</transition>
			<a class="show_nav_btn" href="javascript:;" @click='navShow'>
				<i :class='navBtnIClasss.I1'></i>
				<i :class='navBtnIClasss.I2'></i>
				<i :class='navBtnIClasss.I3'></i>
			</a>
		</nav>
		<div id="blogname">
			<img :src="imgs.blogIcon" :alt="picLose">
			<a href="#">{{blogname}}</a>
		</div>
	</header>
</template>
<script>
	export default {
		name :'blogheader',
		data (){
			return{
				blogname:'博客',
				picLose: "图片丢失",
				navshow: false,
				navBtnIClasss: {
					I1: "",
					I2: "",
					I3: ""
				},
				imgs:{
					blogIcon: "http://img.52z.com/upload/news/image/20180623/20180623032709_55049.jpg",
				},
				navgationlist: [
					{
						title: '首页',
						url: '/'
					},{
						title: '更新',
						url: '/newest'
					},{
						title: '友连',
						url: '#'
					},{
						title: '归档',
						url: '#'
					},{
						title: '留言',
						url: '#'
					},
				]
			}
		},
		methods: {
			navShow: function(){
				var navshow = this.navshow;
				this.navshow = !navshow;
				if( !navshow ){
					this.navBtnIClasss.I1 = 'clockwiseRotate';
					this.navBtnIClasss.I2 = 'slideFadeToRight';
					this.navBtnIClasss.I3 = 'anticlockwiseRotate';
				}else{
					this.navBtnIClasss.I1 = 'clockwiseRotatereverse';
					this.navBtnIClasss.I2 = 'slideFadeOutRight';
					this.navBtnIClasss.I3 = 'anticlockwiseRotatereverse';
				}
			}
		},
		created (){
		}
	};
</script>
<style scoped>
	header{ position: relative; width: 100%; height: 400px; background: rgba(0,0,0,.4); overflow: hidden; }
	header:after{ content: ""; display: block; width: 200%; height: 100%; border-radius: 50%; background: rgba(255,255,255,0.95);
		position: absolute; left: 0; right: 0; bottom: -350px; margin-left: -50%;
	}
	.ground_bg{ background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1555933553406&di=0301c8c36cd81a0b40a71426334a4e5a&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farticle%2Fd2c216c4b538b175fdd3a24bd1a9c479decfd1f3.jpg) top center no-repeat;
		-moz-background-size: cover; -o-background-size: cover; -webkit-background-size: cover; background-size: cover;
		top: 0; left: 0; position: fixed; width: 100%; height: 100%; z-index: -999; }

	nav{ position: fixed; top: 40px; right: 40px; display: flex; justify-content: flex-end; align-items: center; height: 24px; z-index: 9; z-index: 9999; }
	.navgationlist{ display: flex; }
	.navgationlist li a{ display: block; width: 80px; text-align: center; font-size: 16px; font-weight: bold; color: #FFF; }
	.show_nav_btn{ position: relative; width: 30px; height: 24px; display: flex; flex-direction: column; justify-content: space-between; align-items: center; z-index: 1; }
	.show_nav_btn i{ width: 30px; height: 1px; background: #FFF; }

	#blogname{ position: absolute; top: 0; left: 0; width: 100%; height: 100%; display: flex; flex-direction: column; justify-content: center; align-items: center; }
	#blogname img{ width: 100px; border-radius: 50%; }
	#blogname a{ display: block; margin-top: 20px; text-align: center; font-size: 18px; color: #FFF; font-weight: bold; text-shadow: 0 2px 4px rgba(0,0,0,.1); }

	/*---动画---*/
	.shownav-enter-active {
		animation: slideFadeOutRight 1s;
		-webkit-animation: slideFadeOutRight 1s;
	}
	.shownav-leave-active {
		animation: slideFadeToRight 1s;
		-webkit-animation: slideFadeToRight 1s;
	}
	/*---媒体查询---*/
	@media (max-width: 800px){
		header{ height: 300px; }
		header:after{ bottom: -280px; }
		nav{ top: 1em; right: 1em; }
		.navgationlist{ position: fixed; display: block; width: 70%; height: 100%; right: 0; top: 0; background: rgba(30,30,30,.98); padding-top: 100px; box-sizing: border-box; }
		.navgationlist li a{ width: auto; padding: 20px 40px; text-align: left; }
	}
</style>


